* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#root {
  height: 100vh;
  overflow-x: auto;
  width: 100%;
  font-size: clamp(0.7rem, -0.875rem + 6.333333vw, 1.3rem);
}

h1 {
  text-align: center;
  height: 8%;
}

#projectsHolder {
  display: flex;
  height: 92%;
  align-items: flex-start;
  justify-content: space-evenly;
  flex-wrap: wrap;
  gap: 3vh 3vw;
}

main {
  text-align: center;
  vertical-align: center;
  line-height: 100vh
}


.project {
  height: 30vh;
  min-height: fit-content;
  width: 42vw;
  max-width: 250px;
  border: 1px solid gray; 
  padding: 1vh;
  text-align: center;
  border-radius: 2px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
  transition: transform 0.2s;
}

.project:hover, 
.project:active {
  transform: scale(0.9);
  box-shadow: unset;
}

.projectImage {
  height: 80%;
  width: 100%;
}

a {
  text-decoration: unset;
  color: black;
}

img {
  height: 100%;
  width: 100%;
}

.projectName {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 20%;
}